// import Button from "react-bootstrap/Button";
import Button from "@mui/material/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import React, { useEffect, useState, useRef } from "react";
import axios from "axios";
import {
  textValidate,
  numValidate,
  textNumberValidate,
  emailValidate,
} from "react-validations-components";

function SignUpFunc(props) {
  const existEmailInput = useRef(null);
  const existPasswordInput = useRef(null);
  const nameInput = useRef(null);
  const emailInput = useRef(null);
  const passwordInput = useRef(null);
  const cityInput = useRef(null);
  const streetInput = useRef(null);
  const phoneNumberInput = useRef(null);
  const [showUp, setShowSignUp] = useState(false);
  const [showIn, setShowSignIn] = useState(false);
  const [newUser, setNewUser] = useState({
    name: "",
    email: "",
    password: "",
    city: "",
    street: "",
    phoneNumber: null,
  });
  const [existUser, setExistUser] = useState({
    email: "",
    password: "",
  });
  const signIn = () => {
    existEmailInput.current.focus();
    existPasswordInput.current.focus();
    if (
      emailValidate(existEmailInput.current.value).status &
      (existEmailInput.current.value !== "")
    ) {
      if (
        textNumberValidate(existPasswordInput.current.value).status &
        (existPasswordInput.current.value !== "")
      ) {
        setExistUser({
          email: existEmailInput.current.value,
          password: existPasswordInput.current.value,
        });
      } else alert("   -       ");
    } else {
      alert("  -    ");
    }
  };
  useEffect(() => {
    if (existUser.password !== "") {
      axios
        .post("/checkUser", existUser)
        .then((res) => {
          console.log(res.data)
          if (res.data == "") {
            alert("    -   ");
          } else {
            console.log("first")
            props.setUser(res.data);
            handleCloseSignIn();
          }
        })
        .catch((err) => alert(" -    "));
    }
  }, [existUser]);

  const signUp = () => {
    nameInput.current.focus();
    emailInput.current.focus();
    passwordInput.current.focus();
    cityInput.current.focus();
    streetInput.current.focus();
    phoneNumberInput.current.focus();
    if (
      textValidate(nameInput.current.value).status &
      (nameInput.current.value !== "")
    ) {
      if (
        emailValidate(emailInput.current.value).status &
        (emailInput.current.value !== "")
      ) {
        if (
          textNumberValidate(passwordInput.current.value).status &
          (passwordInput.current.value !== "")
        ) {
          if (
            textValidate(cityInput.current.value).status &
            (cityInput.current.value !== "")
          ) {
            if (
              textNumberValidate(streetInput.current.value).status &
              (streetInput.current.value !== "")
            ) {
              if (
                numValidate(phoneNumberInput.current.value).status &
                (phoneNumberInput.current.value !== null)
              ) {
                setNewUser({
                  name: nameInput.current.value,
                  password: passwordInput.current.value,
                  phoneNumber: Number(phoneNumberInput.current.value),
                  email: emailInput.current.value,
                  city: cityInput.current.value,
                  street: streetInput.current.value,
                });
                console.log(newUser);
              } else alert("   -    ");
            } else alert("   -      ");
          } else alert("   -   ");
        } else alert("   -       ");
      } else {
        alert("  -    ");
      }
    } else {
      alert("  -      ");
    }
  };
  const handleCloseSignUp = () => setShowSignUp(false);
  const handleShowSignUp = () => setShowSignUp(true);
  const handleCloseSignIn = () => setShowSignIn(false);
  const handleShowSignIn = () => setShowSignIn(true);

  useEffect(() => {
    if (newUser.phoneNumber !== null) {
      axios
        .post("/insertUser", newUser)
        .then((res) => handleCloseSignUp())
        .catch((err) => alert(" -   "));
    }
  }, [newUser]);

  return (
    <>
      <Button
        id="btn"
        variant="contained"
        color="inherit"
        onClick={handleShowSignUp}
      >
        {" "}
      </Button>
      <Button
        id="btn"
        variant="outlined"
        color="inherit"
        onClick={handleShowSignIn}
      >
        {" "}
      </Button>

      <Modal
        show={showUp}
        onHide={handleCloseSignUp}
        backdrop="static"
        keyboard={false}
      >
        <Form>
          <Modal.Header closeButton>
            <Modal.Title>   FeedFood</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form.Group className="mb-2" controlId="form">
              <Form.Label> </Form.Label>
              <Form.Control
                type="name"
                placeholder="   "
                ref={nameInput}
              />
            </Form.Group>
            <Form.Group className="mb-2" controlId="formBasicEmail">
              <Form.Label> </Form.Label>
              <Form.Control
                type="email"
                placeholder=" "
                ref={emailInput}
              />
            </Form.Group>

            <Form.Group className="mb-2" controlId="formBasicPassword">
              <Form.Label></Form.Label>
              <Form.Control
                type="password"
                placeholder=" -  "
                ref={passwordInput}
              />
            </Form.Group>
            <Form.Group className="mb-2" controlId="form">
              <Form.Label> </Form.Label>
              <Form.Control type="City" ref={cityInput} />
            </Form.Group>
            <Form.Group className="mb-2" controlId="form">
              <Form.Label></Form.Label>
              <Form.Control type="street" ref={streetInput} />
            </Form.Group>
            <Form.Group className="mb-2" controlId="form">
              <Form.Label> </Form.Label>
              <Form.Control
                type="phone"
                placeholder="   "
                ref={phoneNumberInput}
              />
            </Form.Group>
            <Button variant="outlined" type="button" onClick={signUp}>
              
            </Button>
          </Modal.Body>
        </Form>
      </Modal>
      <Modal
        show={showIn}
        onHide={handleCloseSignIn}
        backdrop="static"
        keyboard={false}
      >
        <Form>
          <Modal.Header closeButton>
            <Modal.Title>   FeedFood</Modal.Title>
          </Modal.Header>

          <Modal.Body>
            <Form.Group className="mb-2" controlId="formBasicEmail">
              <Form.Label> </Form.Label>
              <Form.Control
                type="email"
                placeholder=" "
                ref={existEmailInput}
              />
            </Form.Group>

            <Form.Group className="mb-2" controlId="formBasicPassword">
              <Form.Label></Form.Label>
              <Form.Control
                type="password"
                placeholder=" "
                ref={existPasswordInput}
              />
            </Form.Group>
            <Button variant="primary" type="button" onClick={signIn}>
              
            </Button>
          </Modal.Body>
        </Form>
      </Modal>
    </>
  );
}

export default SignUpFunc;
